.btn {
  display: block;

  color: $color-default-white;

  border: none;
  cursor: pointer;
  user-select: none;

  &--primary {
    padding: 21px 0;

    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    text-align: center;

    background-color: $color-secondary;

    @include vp-767 {
      padding: 18px 0;
    }
  }

  &--play {
    width: 56px;
    height: 56px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;


    border-radius: 50%;
    border: 1px solid $color-default-white;
    background-color: rgb(255, 255, 255, 0.2);

    transition: background-color $trans-default;
  }

  &--slider {
    padding: 9px 12px;

    background-color: $color-secondary;

    &_next {
      svg {
        transform: rotate(180deg);
      }
    }
  }

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }
}
